@import url(https://fonts.googleapis.com/css2?family=Kosugi+Maru&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(15, 15, 15);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #111216;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home-overview {
    width: 100%;
    display: flex;
    flex-direction: row;

    height: calc(100vh - 80px);
    justify-content: center;
    align-items: center;
    /* background: rgb(73, 73, 73); */
}

.overview-txt-wrap {
    flex: 3 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.overview-txt-box {
    width: 60%;
    height: 50vh;
    box-shadow: 0 0 20px 0 gray;
    border-radius: 2rem;
}

.overview-txt-box h1 {
    margin: 2rem;
    -webkit-animation: typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
            animation: typing 3.5s steps(40, end),
    blink-caret .75s step-end infinite;
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid rgb(194, 12, 12);
    white-space: nowrap; /* Keeps the content on a single line */
    /* letter-spacing: .15em; Adjust as needed */
}
@-webkit-keyframes typing {
from { width: 0 }
to { width: 100% }
}
@keyframes typing {
from { width: 0 }
to { width: 100% }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
from, to { border-color: transparent }
50% { border-color: rgb(132, 0, 161); }
}
@keyframes blink-caret {
from, to { border-color: transparent }
50% { border-color: rgb(132, 0, 161); }
}

.overview-txt-box p {
    margin: 2rem
}

.somekindofimg {
    flex: 1 1;
}
* {
    --component-width: 60%;
    --card-background: rgb(18, 17, 19);
    --card-border-color: rgb(78, 0, 134);
}

.project-display-wrap {
    margin-top: 2rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-display-overview {
    width: var(--component-width);
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center; 
}

.project-card-warp {
    width: var(--component-width);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 2rem
}

.project-card {
    width: 30%;
    background-color: var(--card-background);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 1rem;
    border: var(--card-border-color) solid 5px;
    margin: 0.5rem 1.1%;
    position: relative;
}

.project-card-img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    border-radius: 1rem 1rem 0 0;
    transition: ease-in 0.2s;
    border-bottom: var(--card-border-color) solid 4px;
}

.project-card:hover .card-overlay {
    opacity: 1;
}

.card-overlay {
    width: 100%;
    height: 400px;
    position: absolute;
    background-color: rgba(255, 255, 255, 0.3);
    opacity: 0;
    transition: ease-in 0.2s;
}

.project-card-title {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
}
